.header__wrap {
    background-color: $bg-color;
    position: sticky;
    top: 0;
    z-index: 100;
    width: 100%;
    text-transform: uppercase;
}

.header {
    color: $font-color-brown;
    display: grid;
    grid-template-columns: (max-content) auto (max-content);
    gap: 40px;
    justify-content: normal;
    align-items: center;
    padding: 30px 20px;
}

.header__menu-mobile {
    display: none;
    z-index: 99999;
}

.header__logo {
    font-size: px-to-rem(28px);
    position: relative;
    text-transform: uppercase;
    font-weight: 700;

    a:hover {
        text-decoration: none;
    }
}

.header__list {
    display: flex;
    gap: 40px;
}

.header__search {
    position: relative;

    .header-links__search-icon {
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        margin-top: -2px;
    }
}

.search__form {
    display: flex;
    input {
        background: transparent;
        display: block;
        width: 100%;
        box-sizing: border-box;
        border: none;
        border-bottom: 1px solid #525252;
        outline: 0;
        padding: 0 0 10px 30px;
    }

    .search-bar__form-empty {
        background-position: 50%;
        background-repeat: no-repeat;
        border: none;
        background-color: transparent;
        z-index: 1030;
        position: relative;
        cursor: pointer;
        display: none;
        width: 30px;
        height: 30px;
        svg {
            width: 100%;
            height: auto;
        }
    }
}

.header__search-mobile {
    display: none;
    border: none;
    outline: none;
    background: transparent;

    &:active, 
    &:focus {
        outline: none;
    }
}

.header__button {
    display: flex;
    gap: 24px;
    align-items: center;
    i {
        font-size: px-to-rem(22px);
    }
    img {
        width: 24px;
    }
}

.header__burger {
    display: none;
}

.header__burger.active::before {
    transform: rotate(45deg);
    width: 100%;
    right: 0;
    top: 5px;
    transition: all 0.3s ease 0s;
}

.header__burger.active::after {
    transform: rotate(-45deg);
    width: 100%;
    left: 0;
    bottom: 5px;
    transition: all 0.3s ease 0s;

}

.header__burger.active span {
    transform: scale(0);
    transition: all 0.3s ease 0s;

}

@media (max-width: 1024px) {

    .header {
        grid-template-columns: (max-content) auto (max-content);
    }

    .header__menu {
        display: none;
    }

    .header__search {
        display: none;
    }

    .header__search-mobile {
        display: block;
    }

    .search__form {
    
        .search-bar__form-empty {
            display: block;
        }
    }

    .header__burger {
        display: block;
        position: relative;
        width: 18px;
        height: 12px;
        z-index: 3;
    
        span {
            position: absolute;
            background-color: $font-color-brown;
            left: 0;
            width: 100%;
            height: 2px;
            top: 5px;
            transition: all 0.3s ease 0s;
        }
    
        &:before {
            content: '';
            background-color: $font-color-brown;
            position: absolute;
            width: 65%;
            height: 2px;
            right: 10%;
            transition: all 0.3s ease 0s;
            top: 0;
        }
    
        &:after {
            content: '';
            background-color: $font-color-brown;
            position: absolute;
            width: 65%;
            height: 2px;
            left: 10%;
            transition: all 0.3s ease 0s;
            bottom: 0;
    
        }
    }

    .header__menu-mobile {
        display: block;
        position: fixed;
        right: -100%;
        overflow: auto;
        width: 100%;
        height: 100%;
        background-color: $light-pink;
        padding: 30px;
        transition: all 0.5s ease 0s;
        max-height: 100vh;
    }

    .header__menu-mobile.active {
        right: 0;
        transition: all 0.5s ease 0s;
        margin-left: auto;
    }

    .header__list {
        display: block;
    }
}

@media (max-width: 500px) {

    .header {
        gap: 20px;
    }

}

